import { format } from 'date-fns';
import { utcToZonedTime } from 'date-fns-tz';
import koLocale from 'date-fns/locale/ko';
import { useState, useEffect } from 'react';

const Header = () => {
  const [time, setTime] = useState('');

  useEffect(() => {
    //   'Asia/Seoul' .
    let koreaTimeZone = 'Asia/Seoul';

    const onChangeTime = () => {
      //  UTC  .
      let nowUtc = new Date();

      // UTC     .
      let nowKoreaTime = utcToZonedTime(nowUtc, koreaTimeZone);

      //       .
      let formattedKoreaTime = format(nowKoreaTime, 'yyyy M d (E) a h m', { locale: koLocale });

      setTime(formattedKoreaTime);
    };

    onChangeTime(); //      

    // 1000ms(=1s) onChangeTime  .
    const intervalId = setInterval(onChangeTime, 1000);

    return () => clearInterval(intervalId); //      
  }, []);

  return (
    <div>
      <h4>Today is </h4>
      <h2 style={{ color: 'brown' }}>{time}</h2>
    </div>
  );
};

export default Header;

