import Header from './components/Header';
import styled from 'styled-components';
import TodoEditor from './components/TodoEditor';
import { useEffect, useRef, useState } from 'react';
import TodoList from './components/TodoList';

const DivApp = styled.div`
  max-width: 750px;
  width: 100%;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid gray;

  display: flex;
  flex-direction: column;
  gap: 30px;
`;

function App() {
  const [text, setText] = useState('');
  const textRef = useRef(null);

  // *  cors   .
  // :  api  cors   .
  const [todoLists, setTodoLists] = useState([]);

  const callData = () => {
    fetch('http://localhost:8080/todolist')
      .then((re) => re.json())
      // .then((data) => setTodoLists(data))
      .then((data) => {
        console.log('good rendering');
        setTodoLists(data);
      })
      .catch((e) => console.error(e));
  };

  // !
  useEffect(callData, []);

  const handleOnChange = (e) => {
    setText(e.target.value);
  };

  const handleOnClickAdd = () => {
    if (text.length === 0) {
      alert('   ');
      textRef.current.focus();
      return;
    }

    fetch('http://localhost:8080/todolist', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        isDone: false,
        content: text,
        createdDate: new Date().toLocaleDateString(),
      }),
    })
      .then((response) => response.json())
      .then((result) => console.log(result))
      // !
      .then(() => callData())
      .then(() => {
        setText('');
        textRef.current.value = '';
        textRef.current.focus();
      });
  };

  const handleOnClickDel = (e) => {
    fetch('http://localhost:8080/todolist', {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        // ! id    button  value  .
        id: e.target.value,
      }),
    })
      .then((response) => response.json())
      .then((result) => console.log(result))
      // !
      .then(() => callData())
      .then(() => textRef.current.focus());
  };

  const handleOnChangeCheck = (e) => {
    fetch('http://localhost:8080/todolist', {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        id: e.target.value,
        // !   (changed) checked  API   .
        isDone: e.target.checked,
      }),
    })
      .then((response) => response.json())
      .then((result) => console.log(result))
      // !
      .then(() => callData());
  };

  return (
    <DivApp>
      <Header />
      <TodoEditor handleOnChange={handleOnChange} handleOnClickAdd={handleOnClickAdd} textRef={textRef} />
      <TodoList todoLists={todoLists} handleOnClickDel={handleOnClickDel} handleOnChangeCheck={handleOnChangeCheck} />
    </DivApp>
  );
}

export default App;
